import React, { Component } from 'react';
import config from './config.json';


export default class SendMail extends Component {
    constructor(props) {
        super(props)
        this.state = {
            title: '',
            recipients: [],
            ccs: [],
            body: '',
            reMailID: this.props.reMailID || '',
            fwMailID: this.props.fwMailID || '',
        }
    }

    render() {
        return (
            <form>
                <div className="box box-primary">
                    <div className="box-header with-border">
                        <h3 className="box-title">寫信</h3>
                    </div>
                    <div className="box-body">
                        <div className="form-group">
                            <label>標題</label>
                            <input className="form-control" placeholder="Title" maxLength="50" onChange={(e) => { this.state.title = e.target.value }} />
                        </div>
                        <div className="form-group">
                            <label>收件人</label>
                            <input className="form-control" placeholder="Recipient" maxLength="42" onChange={this.activeRecipient} />
                        </div>
                        <div className="form-group">
                            <label>副本</label>
                            <input className="form-control" placeholder="CC" maxLength="42" onChange={this.activeCC} />
                        </div>
                        <div className="form-group">
                            <label>內容</label>
                            <textarea
                                onChange={(e) => { this.state.body = e.target.value }}
                                className="form-control"
                                rows="3"
                                placeholder="Text"
                                style={{ "margin": "0px 3px 0px 0px", "width": "100%", "height": "291px" }}>
                            </textarea>
                        </div>
                    </div>
                    <div className="box-footer">
                        <button className="pull-left btn btn-primary" onClick={this.submit}>送件</button>
                    </div>
                </div>
            </form>
        )
    }
}
export { SendMail };